.section-body{
    color: white;
    background-color: #343434;
    margin-bottom: 0;
}
.section-head{
    text-align: left;
    
}
.section-content{
    border-radius: 1rem;
    margin-top: 2rem;
    margin-bottom: 2rem;
    margin-left:8.333% !important;
    color:#343434;
    background-color: white;
}

.section-content img{
    margin-top: 2rem;
    margin-left: 1rem;
    border-radius: 1rem;
}
.profile-img{
    text-align: left;
}
.profile-img img{
    width:300px;
}

.profile-desc{
    margin-top: 1rem;
    text-align: left;
}

.divider{
    background-color: #ED1C24;
}
.desc{
    padding-right: 5%;
}

@media (max-width:450px){
    .profile-img img{
        width:90%;
    }
    .profile-desc .intro{
        text-align: center;
        font-size: 2rem;
    }

    .desc{
        padding-left: 5%;
    }
}